body
{
    background-color: rgb(0, 0, 0);
}

.container
{
    width: 400px;
    height: 650px;
    background-color: rgb(30, 30, 30);
    border-radius: 25px;
    margin: auto;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    box-shadow: -1px -1px 20px rgb(255, 255, 255), 1px 1px 50px rgb(0, 0, 0),
    inset 1px 1px 10px rgb(255, 255, 255), inset -5px -5px 50px rgb(0, 0, 0);
}

#title
{
    font-size: 40px;
    color: wheat;
    font-weight: bold;
}

#textout
{
    width: 380px;
    height: 50px;
    border-radius: 10px;
    background-color: rgb(7, 7, 7);

    font-size: 40px;
    color: #ffffff;
    font-weight: bold;
}

.btn_container
{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

button
{
    width: 80px;
    height: 80px;
    border-radius: 10px;
    background-color: rgb(0, 0, 17);
    border: none;
    margin-left: 1px;
    margin-right: 1px;
    margin-top: 2px;
    margin-bottom: 2px;

    font-size: 40px;
    font-weight: bold;
    color: wheat;

    cursor: pointer;
}

button:active
{
    background-color: rgb(1, 1, 44);
}

#clear_btn
{
    grid-column: 1/3;
    grid-row: 1;
    width: 160px;
    height: 80px;
}

#equal_btn
{
    grid-column: 3/5;
    grid-row: 5;
    width: 160px;
    height: 80px;
}

.highlight
{
    background-color: #1f2c7c;
}

.highlight:active
{
    background-color: #2e3fac;
}